// Buttons
a.btn,
button.btn {
    font-size: 1.1875rem;
    font-family: $header-stack;
    font-weight: $medium;
    display: inline-block;
    border:none;
    cursor:pointer;
    color: $black;
    text-align: center;
    text-transform: uppercase;
    padding: 1rem 2rem;
    position: relative;
    z-index: 1;
    transition: all .5s cubic-bezier(0.34, 1.56, 0.64, 1);
    &::before {
        content: '';
        background: linear-gradient(to right, $blue, rgba(0,0,0,0));
        height: 100%;
        width: 52%;
        position: absolute;
        z-index: -1;
        left: 0;
        top: 0;
        bottom: 0;
        transition: all .5s cubic-bezier(0.34, 1.56, 0.64, 1);
    }
    &:hover {
        &::before {
            width: 100%;;
        }
        // color: $white;
    }
}

// Social
.social {
    a {
        color: $blue;
        display: inline-block;
        margin: 0 .5rem;
        font-size: 1.25rem;
        &:hover {
            color: $black;
        }
    }
}

//Nav
.stellarnav {
    li > .dd-toggle i {
        transition:none;
    }
    li.open > .dd-toggle i {
        transform: rotate(180deg)!important;
    }
    li.open > a {
        font-weight:700;
        text-decoration: underline;
    }
    &.mobile {
        border: solid 1px rgba(255,255,255,.5);
        li.has-sub > ul li a:first-of-type {
            padding-left: 2rem;
        }
        .third {
            width: 33.3333%;
            color: $white;
            padding: 25px 15px;
            font-family: $header-stack;
        }
        .close-menu {
            width: 100%;
            color: $white;
            font-family: $header-stack;
        }
        li > a {
            border-top: solid 1px rgba(255,255,255,.08);
        }
        ul {
            background:none;
        }
    }
    .dd-toggle {
        height: 51px;
        .fa-plus::before {
            content: '\f078';
        }
    }
    ul {
        li {
            a {
                color: $white;
                font-family: $header-stack;
                font-weight: 500;
            }
        }
    }
    &.desktop {
        > ul > li {
        > a {
            padding: 20px 10px;
            @media only screen and (min-width: 1400px) {
                padding: 20px;
            }
        } 

        }
        ul {
            ul {
                padding: 10px;
                background: rgba(#000, .95);
                // width: 300px;
                li {
                    a {
                        padding: 10px 15px;
                    }
                }
                ul {
                    top: -10px!important;
                    left: 210px;
                }
            }
            li {
                a {
                    &:hover {
                        background: $blue;
                    }
                }
            }
        }
    }

}

/* FORM */
form {
    input,
    select,
    textarea {
        font-size: inherit;
        font-family: inherit;
        width: 100%;
        margin: 0 0 10px;
        padding: 15px;
        border: 1px solid $text-base;
        background: #fff;
        transition: all .5s ease;
        &:focus {
            outline:none;
        }
    }
    textarea {
        height: 200px;
    }
    span.agree {
        display: inline-block;
    }
    ::placeholder {
        font-style: normal;
        font-family: inherit;
        font-size: inherit;
    }
}

